.veui-breadcrumb {
  *zoom: 1;
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 16px;
  height: 22px;
  line-height: 22px;
}
.veui-breadcrumb:before,
.veui-breadcrumb:after {
  display: table;
  content: "";
}
.veui-breadcrumb:after {
  clear: both;
}
.veui-breadcrumb-separator {
  margin: 0 4px;
  font-size: 12px;
  color: #666;
}



