/* Metrics */
.veui-pager {
  *zoom: 1;
  display: inline-block;
  color: #666;
}
.veui-pager:before,
.veui-pager:after {
  display: table;
  content: "";
}
.veui-pager:after {
  clear: both;
}
.veui-pager a {
  color: inherit;
  text-decoration: none;
}
.veui-pager a:focus {
  outline: none;
}
.veui-pager-info {
  float: left;
  margin-right: 6px;
  line-height: 30px;
}
.veui-pager-total,
.veui-pager-size {
  display: inline-block;
}
.veui-pager-size {
  margin-right: 10px;
  margin-left: 20px;
}
.veui-pager-switch {
  float: left;
}
.veui-pager-switch .veui-active a {
  color: #333;
}
.veui-pager .veui-active a {
  background-color: #eee;
}
.veui-pager-buttons a {
  transition-property: border-color, background-color, color;
  transition-duration: 0.2s;
}
.veui-pager-buttons a,
.veui-pager-buttons span {
  border: 1px solid #dbdbdb;
  background: transparent;
}
.veui-pager-buttons a:hover,
.veui-pager-buttons span:hover {
  color: #333;
  background-color: #f7f7f7;
}
.veui-pager-buttons a.focus-ring,
.veui-pager-buttons span.focus-ring {
  color: #3998fc;
}
.veui-pager-buttons a:active,
.veui-pager-buttons span:active {
  background-color: #e7e7e7;
}
.veui-pager-buttons a.veui-disabled,
.veui-pager-buttons span.veui-disabled,
.veui-pager-buttons a.veui-disabled:hover,
.veui-pager-buttons span.veui-disabled:hover,
.veui-pager-buttons a.veui-disabled:active,
.veui-pager-buttons span.veui-disabled:active {
  background-color: #eee;
  border-color: transparent;
  color: #b8b8b8;
}
.veui-pager-buttons a .veui-icon,
.veui-pager-buttons span .veui-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.veui-pager .veui-select {
  min-width: auto;
  margin-top: -1px;
  margin-left: 10px;
  vertical-align: top;
}
.veui-pager .veui-select .veui-button {
  padding-top: 0;
  padding-bottom: 0;
  height: 30px;
  line-height: 28px;
  white-space: nowrap;
  color: #333;
}
.veui-pager .veui-select .veui-button .veui-icon {
  margin-top: calc(14px - 0.5em);
}
.veui-pager .veui-select .veui-select-label {
  max-width: none;
  width: auto;
  float: left;
  margin-right: 5px;
}
.veui-pager-buttons {
  float: left;
  margin-left: 6px;
}
.veui-pager-previous,
.veui-pager-next {
  position: relative;
  float: left;
  width: 30px;
  height: 30px;
  line-height: 28px;
  text-align: center;
}
.veui-pager-pages {
  float: left;
  margin: 0;
  padding: 0;
  list-style: none;
}
.veui-pager-pages li {
  float: left;
  width: 28px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 14px;
}
.veui-pager-pages li a,
.veui-pager-pages li span {
  display: inline-block;
  min-width: 30px;
  padding-right: 6px;
  padding-left: 6px;
  cursor: pointer;
}
.veui-pager-pages li a:hover,
.veui-pager-pages li span:hover {
  color: #333;
}
.veui-pager-pages li a.focus-ring,
.veui-pager-pages li span.focus-ring {
  color: #2e7aca;
}
.veui-pager-pages span {
  color: #999;
}
.veui-pager .veui-pager-digit-length-4 li {
  width: 40px;
}
.veui-pager .veui-pager-digit-length-3 li {
  width: 34px;
}
.veui-pager .veui-pager-switch,
.veui-pager[ui~="slim"] .veui-pager-switch {
  padding: 0 36px;
  position: relative;
}
.veui-pager .veui-pager-previous,
.veui-pager[ui~="slim"] .veui-pager-previous {
  position: absolute;
  left: 0;
}
.veui-pager[ui~="hetero"] .veui-pager-switch,
.veui-pager[ui~="full"] .veui-pager-switch {
  padding-left: 0;
}
.veui-pager[ui~="hetero"] .veui-pager-previous,
.veui-pager[ui~="full"] .veui-pager-previous {
  position: relative;
  margin-right: 6px;
}
.veui-pager-info {
  display: none;
}
.veui-pager[ui~="full"] .veui-pager-info {
  display: block;
}
.veui-pager[ui~="slim"] .veui-pager-switch {
  padding: 0 24px;
}
.veui-pager[ui~="slim"] .veui-pager-switch li {
  width: 18.66666667px;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
}
.veui-pager[ui~="slim"] .veui-pager-previous,
.veui-pager[ui~="slim"] .veui-pager-next {
  width: 20px;
  height: 20px;
  line-height: 1.5;
}
.veui-pager[ui~="slim"] .veui-icon {
  height: 10px;
}
.veui-pager[ui~="slim"] .veui-pager-digit-length-4 li {
  width: 32px;
}
.veui-pager[ui~="slim"] .veui-pager-digit-length-3 li {
  width: 27.2px;
}



