.veui-check-button-group .veui-button.veui-button-selected {
  background-color: #e7e7e7;
  color: #333;
  z-index: 1;
}
.veui-check-button-group .veui-button.veui-button-selected.focus-ring {
  color: #3998fc;
}
.veui-check-button-group .veui-button[ui~="alt"].veui-button-selected {
  background-color: #dbdbdb;
}
.veui-check-button-group .veui-button.veui-disabled,
.veui-check-button-group .veui-button.veui-disabled:hover {
  border-right-color: #fff;
  border-left-color: #fff;
}
.veui-check-button-group .veui-button.veui-disabled.veui-button-selected {
  background-color: #dbdbdb;
  color: #999;
}



