/* Metrics */
.veui-tabs {
  padding-bottom: 1px;
}
.veui-tabs ul,
.veui-tabs li {
  list-style: none;
}
.veui-tabs-menu {
  border-bottom: 1px solid #e7e7e7;
}
.veui-tabs-list {
  *zoom: 1;
  padding: 0;
  margin: 0;
  margin-bottom: -1px;
}
.veui-tabs-list:before,
.veui-tabs-list:after {
  display: table;
  content: "";
}
.veui-tabs-list:after {
  clear: both;
}
.veui-tabs-item {
  float: left;
  margin-left: 60px;
}
.veui-tabs-item:first-child {
  margin-left: 0;
}
.veui-tabs-item > a,
.veui-tabs-item > span {
  cursor: pointer;
  padding: 0 10px;
  line-height: 2.5;
  display: inline-block;
  text-decoration: none;
  outline: none;
  position: relative;
}
.veui-tabs-item > a::after,
.veui-tabs-item > span::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 10px;
  width: calc(100% - 20px);
  height: 2px;
}
.veui-tabs-item > a:hover::after,
.veui-tabs-item > span:hover::after {
  background-color: #999;
}
.veui-tabs-item-active > a::after,
.veui-tabs-item:active > a::after,
.veui-tabs-item-active > span::after,
.veui-tabs-item:active > span::after {
  background-color: #333;
}
.veui-tabs-item:first-child > a,
.veui-tabs-item:first-child > span {
  margin-left: -10px;
}
.veui-tabs[ui~="large"] .veui-tabs-item {
  font-size: 16px;
  margin-left: 60px;
}
.veui-tabs[ui~="large"] .veui-tabs-item:first-child {
  margin-left: 0;
}
.veui-tabs[ui~="small"] .veui-tabs-item {
  margin-left: 40px;
}
.veui-tabs[ui~="small"] .veui-tabs-item:first-child {
  margin-left: 0;
}
.veui-tabs[ui~="small"] .veui-tabs-menu {
  border-bottom: none;
}
.veui-tabs[ui~="tiny"] .veui-tabs-menu {
  border-bottom: none;
}
.veui-tabs[ui~="tiny"] .veui-tabs-item {
  margin-left: 20px;
  position: relative;
}
.veui-tabs[ui~="tiny"] .veui-tabs-item:first-child {
  margin-left: 0;
}
.veui-tabs[ui~="tiny"] .veui-tabs-item::before {
  content: "";
  display: inline-block;
  width: 1px;
  height: 14px;
  background-color: #e7e7e7;
  position: absolute;
  top: 10px;
  left: -10px;
}
.veui-tabs[ui~="tiny"] .veui-tabs-item:first-child::before {
  display: none;
}



