/* Metrics */
.veui-region-picker {
  width: 738px;
  border: 1px solid #dbdbdb;
}
.veui-region-picker-section {
  border-top: 1px solid #dbdbdb;
}
.veui-region-picker-section:first-child {
  border-top: none;
}
.veui-region-picker-section-title {
  height: 40px;
  line-height: 40px;
  padding-left: 20px;
}
.veui-region-picker-branch {
  padding: 0 20px;
}
.veui-region-picker-branch:hover {
  background-color: #f7f7f7;
}
.veui-region-picker-branch-title {
  float: left;
  margin: 16px 0;
  width: 116px;
}
.veui-region-picker-branch-title::after {
  content: "：";
  line-height: 1;
}
.veui-region-picker-branch-content {
  overflow: hidden;
}
.veui-region-picker-group {
  float: left;
  margin: 16px 0;
}
.veui-region-picker-group-title {
  width: 116px;
}
.veui-region-picker-group-overlay {
  position: relative;
}
.veui-region-picker-group-shadow {
  float: left;
  margin: -11px 0 0 -8px;
  padding: 10px 8px;
  border-radius: 2px 2px 0;
  background-color: #fff;
  border-top: 1px solid #f7f7f7;
  z-index: 1;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}
.veui-region-picker-group-shadow small {
  margin-left: 10px;
  font-size: 14px;
  color: #999;
}
.veui-region-picker-group-shadow::before,
.veui-region-picker-group-shadow::after {
  content: "";
  display: block;
  position: absolute;
  bottom: -5px;
  left: -8px;
  width: calc(100% + 8px);
  height: 10px;
  background-color: #fff;
}
.veui-region-picker-group-shadow::after {
  bottom: -10px;
  left: 20px;
}
.tether-element-attached-bottom + .veui-region-picker-group-shadow-overlay .veui-region-picker-group-shadow::before,
.tether-element-attached-bottom + [data-tether-id] + .veui-region-picker-group-shadow-overlay .veui-region-picker-group-shadow::before {
  top: -15px;
  bottom: auto;
}
.tether-element-attached-bottom + .veui-region-picker-group-shadow-overlay .veui-region-picker-group-shadow::after,
.tether-element-attached-bottom + [data-tether-id] + .veui-region-picker-group-shadow-overlay .veui-region-picker-group-shadow::after {
  top: -20px;
  bottom: auto;
}
.veui-region-picker-units {
  display: table;
  margin: 10px 0 0 -8px;
  background-color: #fff;
  border-top: 1px solid #f7f7f7;
  z-index: 0;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}
.tether-element-attached-bottom .veui-region-picker-units {
  margin: 0 0 10px -8px;
}
.veui-region-picker-unit-row {
  display: table-row;
  margin: 10px 8px;
}
.veui-region-picker-unit {
  display: table-cell;
  padding: 10px 20px 10px 8px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.veui-region-picker-unit:first-child {
  padding-left: 20px;
}
.veui-region-picker-unit-row:first-child .veui-region-picker-unit {
  padding-top: 20px;
}
.veui-region-picker-unit-row:last-child .veui-region-picker-unit {
  padding-bottom: 20px;
}



