/* Metrics */
.veui-uploader {
  width: 400px;
  overflow: hidden;
}
.veui-uploader-button-container {
  position: relative;
  margin-bottom: 20px;
  overflow: hidden;
}
.veui-uploader-button-container .veui-uploader-tip {
  margin-top: 8px;
  display: inline-block;
}
.veui-uploader-input-label {
  float: left;
  color: #333;
  margin-right: 1em;
  cursor: pointer;
}
.veui-uploader-input-label-icon {
  margin-right: 5px;
}
.veui-uploader .veui-uploader-input-label-disabled,
.veui-uploader .veui-button.veui-uploader-input-label-disabled {
  cursor: not-allowed;
  border-color: transparent;
  background-color: #eee;
  color: #999;
  box-shadow: none;
}
.veui-uploader .veui-uploader-input-label-disabled:hover,
.veui-uploader .veui-button.veui-uploader-input-label-disabled:hover {
  border-color: transparent;
  background-color: #eee;
  color: #999;
  box-shadow: none;
}
.veui-uploader-input-label-image {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.veui-uploader-input-label-image .veui-icon {
  font-size: 50px;
  vertical-align: middle;
  color: #666;
}
.veui-uploader-input-label-image:hover {
  background: #f7f7f7;
}
.veui-uploader-input-label-image:active {
  background: #e7e7e7;
}
.veui-uploader-list,
.veui-uploader-list-image {
  padding: 0;
  margin: 0;
  list-style: none;
  overflow: hidden;
}
.veui-uploader-list img,
.veui-uploader-list-image img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  vertical-align: middle;
}
.veui-uploader-list-status,
.veui-uploader-list-image-status {
  line-height: 1.2;
}
.veui-uploader-list-status > span,
.veui-uploader-list-image-status > span {
  display: inline-block;
  vertical-align: middle;
  word-break: break-all;
}
.veui-uploader-list-status::after,
.veui-uploader-list-image-status::after {
  content: "";
  height: 100%;
  display: inline-block;
  vertical-align: middle;
}
.veui-uploader-list-success,
.veui-uploader-list-image-success {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 9px 4px;
  background-color: #fff;
  color: #5bc49f;
  cursor: pointer;
}
.veui-uploader-list {
  width: 300px;
}
.veui-uploader-list li {
  position: relative;
  padding: 4px;
  margin: 0 10px 5px 0;
  width: 300px;
  color: #666;
}
.veui-uploader-list li:hover {
  background-color: #f7f7f7;
}
.veui-uploader-list li:hover .veui-button[ui~="link"] {
  background-color: #f7f7f7;
}
.veui-uploader-list li:hover .veui-button[ui~="remove"] {
  visibility: visible;
}
.veui-uploader-list-name {
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  width: calc(100% - 3em);
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 20px;
}
.veui-uploader-list-name-success {
  width: calc(100% - 9em);
}
.veui-uploader-list-name-failure {
  width: calc(100% - 11em);
}
.veui-uploader-list-status {
  display: inline-block;
  width: calc(100% - 3em);
  height: 100%;
}
.veui-uploader-list-success .veui-icon {
  position: relative;
  top: 0;
}
.veui-uploader-list-icon {
  width: 12px;
  height: 12px;
  margin: 4px 5px 0 0;
  vertical-align: top;
}
.veui-uploader-list .veui-button {
  float: right;
  height: 20px;
  line-height: 20px;
  vertical-align: top;
}
.veui-uploader-list .veui-button.veui-uploader-list-retry {
  float: none;
  margin-left: 10px;
  color: #333;
}
.veui-uploader-list .veui-button.veui-uploader-list-retry .veui-icon {
  margin: 4px 6px 0 0;
  font-size: 12px;
}
.veui-uploader-list .veui-uploader-progress {
  display: inline-block;
  width: calc(100% - 6em);
  height: 20px;
  vertical-align: middle;
}
.veui-uploader-list-image li {
  overflow: hidden;
  width: 140px;
  height: 140px;
  margin: 0 10px 5px 0;
  padding: 0;
  border: 1px solid #dbdbdb;
  text-align: center;
  line-height: 138px;
  position: relative;
}
.veui-uploader-list-image li .veui-button[ui~="operation"] {
  position: absolute;
  top: calc(50% + 10px);
  left: calc(50% - 35px);
  width: 70px;
  height: 28px;
  line-height: 28px;
  font-size: 12px;
}
li:hover .veui-uploader-list-image-mask {
  display: block;
}
.veui-uploader-list-image-mask {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.1);
}
.veui-uploader-list-image-mask .veui-button {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  font-size: 14px;
}
.veui-uploader-list-image-mask .veui-button:hover {
  background-color: rgba(0, 0, 0, 0.8);
}
.veui-uploader-list-image-mask .veui-button:hover,
.veui-uploader-list-image-mask .veui-button.active {
  color: #fff;
}
.veui-uploader-list-image-mask label.veui-button {
  cursor: pointer;
  top: calc(50% - 14px);
  left: calc(50% - 44px);
  width: 88px;
  height: 28px;
  line-height: 28px;
}
.veui-uploader-list-image-mask-remove {
  width: 24px;
  height: 24px;
  line-height: 24px;
  top: 0;
  right: 0;
  left: auto;
  overflow: hidden;
}
.veui-uploader-list-image-mask-remove .veui-icon {
  margin-top: 5px;
}
.veui-uploader-list-image-mask .veui-button.veui-uploader-input-label-disabled {
  background: none;
}
.veui-uploader-list-image-mask .veui-button.veui-uploader-input-label-disabled:hover {
  background: none;
}
.veui-uploader-list-image-status {
  margin-top: 10%;
  height: 40%;
}
.veui-uploader-list-image-status > span {
  max-width: 90%;
}
.veui-uploader-list-image-success {
  padding: 0;
  line-height: 138px;
}
.veui-uploader-list-image-success span {
  display: block;
  vertical-align: middle;
}
.veui-uploader-list-image-success .veui-icon {
  font-size: 16px;
  vertical-align: text-bottom;
  margin-right: 5px;
}
.veui-uploader-list-image .veui-uploader-progress-bar,
.veui-uploader-list-image .veui-uploader-progress-bar-full {
  left: calc(50% - 35px);
  max-width: 70px;
}
.veui-uploader-tip {
  color: #999;
}
.veui-uploader-failure {
  color: #ff5b5b;
}
.veui-uploader-success {
  color: #5bc49f;
}
.veui-uploader-hide {
  display: none;
}
.veui-uploader[ui~="horizontal"] {
  width: 100%;
}
.veui-uploader[ui~="horizontal"] .veui-uploader-list li,
.veui-uploader[ui~="horizontal"] .veui-uploader-list-image li {
  float: left;
  margin-right: 10px;
}
.veui-uploader .veui-button[ui~="remove"] {
  float: right;
  color: #666;
  visibility: hidden;
  height: 20px;
  line-height: 20px;
}
.veui-uploader .veui-button[ui~="remove"] .veui-icon {
  font-size: 10px;
  margin-top: 4px;
}
.veui-uploader-list-enter,
.veui-uploader-list-leave-active {
  opacity: 0;
}
.veui-uploader-list-enter-active,
.veui-uploader-list-leave-active {
  transition: all .3s ease;
}
.veui-uploader-list-move {
  transition: transform .3s;
}
.veui-uploader-fade-leave-active {
  opacity: 0;
  transition: all .3s ease;
}
.veui-uploader-progress {
  display: block;
  position: relative;
  line-height: 20px;
}
.veui-uploader-progress-bar,
.veui-uploader-progress-bar-full {
  position: absolute;
  height: 4px;
  top: calc(50% - 2px);
  width: 100%;
}
.veui-uploader-progress-bar {
  background-color: #666;
}
.veui-uploader-progress-bar-full {
  z-index: -1;
  background-color: #dbdbdb;
}



