/* Metrics */
.veui-steps {
  display: inline-block;
}
.veui-steps-step {
  display: block;
  float: left;
  text-decoration: none;
  position: relative;
  margin-right: 160px;
}
.veui-steps-step-index {
  position: absolute;
  top: calc(50% - 15px);
  width: 30px;
  height: 30px;
  border: 1px solid #5bc49f;
  text-align: center;
  line-height: 28px;
  color: #5bc49f;
  font-size: 16px;
  border-radius: 50%;
}
.veui-steps-step-index .veui-icon {
  vertical-align: top;
  margin-top: calc(50% - 0.5em);
}
.veui-steps-step-content {
  padding-left: 40px;
}
.veui-steps-step:last-child .veui-steps-step-content {
  margin-right: 0;
}
.veui-steps-step-label {
  margin: 0;
  font-size: 16px;
  color: #666;
  line-height: 30px;
  font-weight: 400;
}
.veui-steps-step-desc {
  display: none;
  margin: 0;
  color: #999;
  font-size: 12px;
}
.veui-steps-step::after {
  width: 120px;
  height: 1px;
  content: "";
  position: absolute;
  top: calc(50% - 0.5px);
  right: -140px;
  background-color: #666;
  pointer-events: none;
}
.veui-steps-step:last-child::after {
  display: none;
}
a.veui-steps-step:hover .veui-steps-step-label,
a.veui-steps-step.focus-ring .veui-steps-step-label {
  color: #333;
}
a.veui-steps-step:hover .veui-steps-step-index,
a.veui-steps-step.focus-ring .veui-steps-step-index {
  border-color: #52b08f;
  color: #52b08f;
}
.veui-steps-current .veui-steps-step-index {
  border-color: transparent;
  background-color: #666;
  color: #fff;
}
.veui-steps-current .veui-steps-step::after {
  background-color: #dbdbdb;
}
.veui-steps-current::after,
.veui-steps-current ~ .veui-steps-step::after {
  background-color: #dbdbdb;
}
.veui-steps-current ~ .veui-steps-step .veui-steps-step-index {
  background-color: #dbdbdb;
  border-color: transparent;
  color: #fff;
}
a.veui-steps-current:hover .veui-steps-step-index,
a.veui-steps-current.focus-ring .veui-steps-step-index {
  background-color: #333;
  border-color: transparent;
  color: #fff;
}
.veui-steps-current ~ a.veui-steps-step:hover .veui-steps-step-index,
.veui-steps-current ~ a.veui-steps-step.focus-ring .veui-steps-step-index {
  background-color: #b8b8b8;
  border-color: transparent;
  color: #fff;
}
.veui-steps[ui~="full"] .veui-steps-step-content {
  padding-left: 40px;
}
.veui-steps[ui~="full"] .veui-steps-step-index {
  top: calc(50% - 15px);
  width: 30px;
  height: 30px;
  line-height: 28px;
}
.veui-steps[ui~="full"] .veui-steps-step-label,
.veui-steps[ui~="full"] .veui-steps-step-desc {
  display: block;
  line-height: 21px;
}
.veui-steps[ui~="vertical"] .veui-steps-step {
  float: none;
  margin-right: 0;
  margin-bottom: 80px;
}
.veui-steps[ui~="vertical"] .veui-steps-step::after {
  width: 1px;
  height: 60px;
  top: 40px;
  left: 14.5px;
}
.veui-steps[ui~="vertical"][ui~="full"] .veui-steps-step {
  margin-bottom: 68px;
}
.veui-steps[ui~="vertical"][ui~="full"] .veui-steps-step::after {
  top: 46px;
  left: 14.5px;
}
.veui-steps[ui~="small"] .veui-steps-step {
  margin-right: 100px;
}
.veui-steps[ui~="small"] .veui-steps-step-index {
  top: calc(50% - 10px);
  width: 20px;
  height: 20px;
  line-height: 18px;
  font-size: 14px;
}
.veui-steps[ui~="small"] .veui-steps-step-index .veui-icon {
  font-size: 0.66666667em;
}
.veui-steps[ui~="small"] .veui-steps-step-content {
  padding-left: 30px;
}
.veui-steps[ui~="small"] .veui-steps-step-label {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}
.veui-steps[ui~="small"] .veui-steps-step-desc {
  font-size: 12px;
}
.veui-steps[ui~="small"] .veui-steps-step::after {
  width: 60px;
  height: 1px;
  top: calc(50% - 0.5px);
  right: -80px;
}
.veui-steps[ui~="small"][ui~="full"] .veui-steps-step-content {
  padding-left: 30px;
}
.veui-steps[ui~="small"][ui~="full"] .veui-steps-step-index {
  top: calc(50% - 10px);
  width: 20px;
  height: 20px;
  line-height: 18px;
}
.veui-steps[ui~="small"][ui~="full"] .veui-steps-step-label,
.veui-steps[ui~="small"][ui~="full"] .veui-steps-step-desc {
  line-height: 21px;
}
.veui-steps[ui~="small"][ui~="vertical"] .veui-steps-step {
  margin-bottom: 60px;
}
.veui-steps[ui~="small"][ui~="vertical"] .veui-steps-step::after {
  width: 1px;
  height: 40px;
  top: 30px;
  left: 9.5px;
}
.veui-steps[ui~="small"][ui~="vertical"][ui~="full"] .veui-steps-step {
  margin-bottom: 38px;
}
.veui-steps[ui~="small"][ui~="vertical"][ui~="full"] .veui-steps-step::after {
  top: 240.5px;
  left: 9.5px;
}



