/* Metrics */
.veui-field-invalid .veui-input,
.veui-field-invalid .veui-input:hover,
.veui-field-invalid .veui-input:focus,
.veui-field-invalid .veui-textarea,
.veui-field-invalid .veui-textarea:hover,
.veui-field-invalid .veui-textarea:focus {
  border-color: #ff5b5b;
}
.veui-input,
.veui-textarea {
  height: 36px;
  line-height: 34px;
  width: 210px;
  padding: 0 10px;
  vertical-align: middle;
  border: 1px solid #dbdbdb;
  background-color: #fff;
  color: #666;
}
.veui-input:hover,
.veui-textarea:hover {
  border-color: #999;
}
.veui-input:focus,
.veui-textarea:focus {
  outline: none;
  border-color: #999;
  color: #333;
}
.veui-input:read-only,
.veui-textarea:read-only,
.veui-input:disabled,
.veui-textarea:disabled {
  background-color: #f7f7f7;
  border-color: #dbdbdb;
  color: #999;
}
.veui-input:disabled,
.veui-textarea:disabled {
  cursor: not-allowed;
}
.veui-input::placeholder,
.veui-textarea::placeholder {
  color: #999;
}
.veui-input[ui~="large"],
.veui-textarea[ui~="large"] {
  height: 42px;
  line-height: 40px;
  font-size: 16px;
}
.veui-input[ui~="small"],
.veui-textarea[ui~="small"] {
  height: 32px;
  line-height: 30px;
  font-size: 12px;
}
.veui-textarea {
  padding: 6.5px 10px;
  line-height: 1.5;
  resize: none;
}
.veui-textarea[rows] {
  height: auto;
}
.veui-textarea-resizable {
  resize: both;
}



