/* Metrics */
.veui-dialog {
  display: none;
}
.veui-dialog-box {
  position: fixed;
}
.veui-dialog-draggable {
  cursor: all-scroll;
}
.veui-dialog-content {
  background: #fff;
  position: absolute;
  width: 400px;
  max-height: 100vh;
  overflow-y: auto;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}
.veui-dialog-content-head,
.veui-dialog-content-foot {
  padding: 0 20px;
}
.veui-dialog-content-head {
  height: 56px;
  line-height: 56px;
}
.veui-dialog-content-head-title,
.veui-dialog-content-head-close,
.veui-dialog-content-head-close:hover {
  color: #333;
}
.veui-dialog-content-head-title {
  font-weight: 400;
  font-size: 16px;
}
.veui-dialog-content-head-close,
.veui-dialog-content-head-close:hover {
  float: right;
  width: 16px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}
.veui-dialog-content-body {
  padding-top: 0;
  margin: 10px 20px 0;
}
.veui-dialog-content-foot {
  padding: 20px;
  text-align: center;
}
.veui-dialog-content-foot .veui-button {
  min-width: 94px;
  margin-right: 20px;
}
.veui-dialog-content-foot .veui-button:last-child {
  margin-right: 0;
}
.veui-dialog-box,
.veui-dialog-content {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.veui-dialog-box[ui~="top"],
.veui-dialog-box[ui~="top"] .veui-dialog-content {
  top: 38.2%;
  left: 50%;
  transform: translate(-50%, -38.2%);
}
.veui-dialog-box-mask {
  background-color: rgba(51, 51, 51, 0.5);
  overflow: auto;
}
.veui-dialog-box-mask,
.veui-dialog-box-mask[ui~="top"] {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: none;
}



