.veui-toast-list {
  position: fixed;
  top: 85px;
  right: 0;
  left: 0;
}
.veui-toast-list .veui-toast {
  left: 50%;
  transform: translateX(-50%);
}
.veui-toast-list .veui-toast-enter,
.veui-toast-list .veui-toast-leave-to {
  opacity: 0;
}
.veui-toast-list .veui-toast-enter {
  transform: translate(-50%, -100%);
}



