/* Metrics */
.veui-progress {
  *zoom: 1;
  display: inline-block;
  position: relative;
  padding-right: 10px;
  padding-left: 10px;
  color: #999;
  transition: color .2s;
}
.veui-progress:before,
.veui-progress:after {
  display: table;
  content: "";
}
.veui-progress:after {
  clear: both;
}
.veui-progress-bar {
  height: 36px;
  line-height: 36px;
}
.veui-progress-bar .veui-progress-rail {
  width: 250px;
  height: 4px;
  overflow: hidden;
  float: left;
  margin-top: 16px;
  background-color: #dbdbdb;
}
.veui-progress-bar .veui-progress-meter {
  width: 100%;
  height: 100%;
  margin-left: -100%;
  background-color: #666;
  transition: transform 0.2s, background-color 0.2s;
}
.veui-progress-circular {
  height: auto;
}
.veui-progress-circular .veui-progress-circle {
  display: block;
  transform: rotate(-90deg);
}
.veui-progress-circular .veui-progress-rail {
  stroke: #dbdbdb;
}
.veui-progress-circular .veui-progress-meter {
  stroke: #666;
  transition: stroke-dashoffset 0.2s;
}
.veui-progress-desc {
  float: left;
  padding-left: 10px;
}
.veui-progress[ui~="tiny"] .veui-progress-desc-text {
  display: none;
}
.veui-progress[ui~="tiny"] .veui-icon {
  font-size: 20px;
}
.veui-progress-circular .veui-progress-desc {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 20px;
  line-height: 20px;
  padding-left: 0;
  font-size: 16px;
}
.veui-progress-circular .veui-progress-desc .veui-icon {
  vertical-align: top;
  margin-top: calc(10px - 0.5em);
}
.veui-progress-circular .veui-progress-desc .veui-icon + .veui-progress-desc-text {
  margin-left: 5px;
}
.veui-progress-state-complete {
  color: #666;
}
.veui-progress-state-success {
  color: #5bc49f;
}
.veui-progress-state-success .veui-progress-meter {
  background-color: #5bc49f;
  stroke: #5bc49f;
}
.veui-progress-state-alert {
  color: #ff5b5b;
}
.veui-progress-state-alert .veui-progress-meter {
  background-color: #ff5b5b;
  stroke: #ff5b5b;
}



