.veui-toast {
  position: absolute;
  max-width: 500px;
  max-height: 62px;
  padding: 10px 30px;
  background-color: #fff;
  line-height: 1.5;
  border-radius: 1px;
  color: #666;
  transition: opacity .3s, top .3s, transform .3s;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}
.veui-toast-message {
  display: block;
  margin-left: 36px;
}
.veui-toast-icon {
  position: absolute;
  top: 50%;
  left: 30px;
  transform: translateY(-50%);
  font-size: 16px;
}
.veui-toast-success .veui-toast-icon {
  color: #5bc49f;
}
.veui-toast-warning .veui-toast-icon {
  color: #fe9700;
}
.veui-toast-info .veui-toast-icon {
  color: #3998fc;
}
.veui-toast-error .veui-toast-icon {
  color: #ff5b5b;
}



