/* Metrics */
.veui-checkbox {
  display: inline-block;
  color: #666;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
}
.veui-checkbox input,
.veui-checkbox .veui-icon {
  display: none;
}
.veui-checkbox-label {
  vertical-align: middle;
}
.veui-field .veui-checkbox {
  line-height: 36px;
}
.veui-checkbox:hover {
  color: #333;
}
.veui-checkbox-box {
  display: inline-block;
  position: relative;
  height: 16px;
  width: 16px;
  margin-right: 8px;
  border: 1px solid #666;
  background-color: #fff;
  font-size: 16px;
  vertical-align: middle;
}
.veui-checkbox:hover .veui-checkbox-box {
  border-color: #333;
}
.veui-checkbox :checked + .veui-checkbox-box,
.veui-checkbox :indeterminate + .veui-checkbox-box {
  border-color: #333;
}
.veui-checkbox :checked + .veui-checkbox-box > .veui-icon,
.veui-checkbox :indeterminate + .veui-checkbox-box > .veui-icon {
  display: block;
  color: #333;
  position: absolute;
  top: 50%;
  right: 50%;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.veui-checkbox.veui-disabled {
  cursor: not-allowed;
}
.veui-checkbox.veui-disabled .veui-checkbox-label {
  color: #999;
}
.veui-checkbox.veui-disabled .veui-checkbox-box {
  border-color: #dbdbdb;
  background-color: #eee;
}
.veui-checkbox.veui-disabled .veui-checkbox-box .veui-icon {
  color: #dbdbdb;
}
.veui-checkbox[ui~="small"] {
  font-size: 12px;
  line-height: 12px;
}
.veui-checkbox[ui~="small"] .veui-checkbox-box {
  margin-right: 6px;
  height: 12px;
  width: 12px;
  font-size: 12px;
}
.veui-checkbox[ui~="small"] .veui-checkbox-box + span {
  font-size: 12px;
}



