/* Metrics */
.veui-option {
  transition-property: border-color, background-color, color;
  transition-duration: 0.2s;
  height: 36px;
  line-height: 36px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  padding: 0 10px;
  color: #666;
  cursor: pointer;
}
.veui-option:hover {
  background-color: #f7f7f7;
}
.veui-option:active {
  background-color: #e7e7e7;
}
.veui-option-selected {
  color: #333;
}
.veui-option-disabled {
  background-color: #fff;
  color: #999;
  cursor: not-allowed;
}
.veui-option-label {
  margin-right: 10px;
}
.veui-option-checkmark {
  float: right;
  display: none;
}
.veui-option[ui~="checkmark"] .veui-option-checkmark {
  display: block;
  margin-top: calc(18px - .5em);
}



