#app {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
#main-nav,
#console {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
}
#main-nav {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 240px;
  border-right: 1px solid #eee;
  font-weight: 300;
}
#main-nav h1 {
  height: 60px;
  line-height: 60px;
  position: relative;
  overflow: hidden;
  border-bottom: 1px solid #eee;
  margin: 0;
  font-size: 18px;
}
#main-nav h1 a {
  display: block;
  width: 100%;
  padding: 0 24px;
  color: #333;
  text-decoration: none;
}
#main-nav h1 .veui-icon {
  position: absolute;
  opacity: .05;
  z-index: -1;
  top: -25px;
  right: -5px;
  transition: transform .6s;
}
#main-nav h1:hover .veui-icon {
  transform: scale(0.8);
  opacity: .1;
}
#main-nav ul {
  height: calc(100vh - 105px);
  margin: 0;
  padding: 0;
  overflow: auto;
}
#main-nav li a {
  display: block;
  padding: .5em 24px;
  text-decoration: none;
  color: #999;
  outline: none;
  line-height: 2;
}
#main-nav li a:hover {
  font-weight: 500;
}
#main-nav .router-link-active {
  color: #3998fc;
  background-color: #f6f9ff;
  font-weight: 500;
}
#main-nav footer {
  position: relative;
  overflow: hidden;
  border-top: 1px solid #eee;
  font-size: 12px;
}
#main-nav footer a {
  display: block;
  width: 100%;
  padding-left: 24px;
  line-height: 45px;
  color: #999;
  text-decoration: none;
}
#main-nav footer .veui-icon {
  position: absolute;
  opacity: .05;
  z-index: -1;
  top: -36px;
  right: -5px;
  transition: transform 0.3s ease-in-out;
}
#main-nav footer:hover .veui-icon {
  transform: translateY(10px) scale(0.3);
  opacity: .1;
}
main {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 240px;
  overflow: scroll;
  padding: 1em 4em 0;
  height: calc(100vh - 30px);
  transition: height .2s;
}
main h1 {
  border-bottom: 1px solid #eee;
  margin-bottom: 2em;
  padding-bottom: 1em;
  font-size: 18px;
}
.console-expanded main {
  height: calc(100vh - 40vh - 30px);
}
#console {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 240px;
}



