/* Metrics */
.veui-dropdown-options {
  border: 1px solid #e7e7e7;
  margin: 3px 0;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}
.veui-dropdown-button {
  width: 100%;
  padding-right: 10px;
  padding-left: 10px;
  text-align: left;
}
.veui-dropdown-label {
  display: inline-block;
  max-width: calc(100% - 24px);
  width: 100%;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.veui-dropdown-icon {
  float: right;
}
.veui-dropdown-button[ui~="primary"]:not(.veui-button-loading):disabled {
  background-color: #eee;
  border-color: transparent;
  color: #999;
}
.veui-dropdown-expanded .veui-dropdown-button:not([ui~="link"]),
.veui-dropdown-expanded .veui-dropdown-button:not([ui~="link"]):hover,
.veui-dropdown-expanded .veui-dropdown-button:not([ui~="link"]).focus-ring {
  color: #333;
  background-color: #f7f7f7;
}
.veui-dropdown-expanded .veui-dropdown-button[ui~="primary"]:not([ui~="link"]),
.veui-dropdown-expanded .veui-dropdown-button[ui~="primary"]:not([ui~="link"]):hover,
.veui-dropdown-expanded .veui-dropdown-button[ui~="primary"]:not([ui~="link"]).focus-ring {
  background-color: #3389e3;
  color: #fff;
}
.veui-dropdown-expanded .veui-dropdown-button[ui~="primary"]:not([ui~="link"]):active {
  background-color: #2e7aca;
}
.veui-dropdown-expanded .veui-dropdown-button[ui~="alt"],
.veui-dropdown-expanded .veui-dropdown-button[ui~="alt"]:hover,
.veui-dropdown-expanded .veui-dropdown-button[ui~="alt"].focus-ring {
  background-color: #e7e7e7;
}
.veui-dropdown-option {
  transition-property: border-color, background-color, color;
  transition-duration: 0.2s;
  height: 36px;
  line-height: 36px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  padding: 0 10px;
  color: #666;
  cursor: pointer;
}
.veui-dropdown-option:hover {
  background-color: #f7f7f7;
}
.veui-dropdown-option:active {
  background-color: #e7e7e7;
}
.veui-dropdown-option-selected {
  color: #333;
}
.veui-dropdown-option-disabled {
  background-color: #fff;
  color: #999;
  cursor: not-allowed;
}
.veui-dropdown-option-label {
  margin-right: 10px;
}
.veui-dropdown {
  display: inline-block;
  min-width: 110px;
}
.veui-dropdown-options {
  min-width: 110px;
  max-height: 280px;
  overflow-y: auto;
  background-color: #fff;
}
.veui-dropdown[ui~="link"] {
  min-width: auto;
}
.veui-dropdown[ui~="link"] .veui-dropdown-label {
  width: auto;
  max-width: none;
  margin-right: 5px;
  vertical-align: middle;
}
.veui-dropdown[ui~="link"] .veui-button {
  width: auto;
  height: auto;
  line-height: 1.5;
}
.veui-dropdown[ui~="link"] .veui-button:not(:disabled) {
  color: #3998fc;
}
.veui-dropdown[ui~="link"] .veui-button:not(:disabled):hover,
.veui-dropdown[ui~="link"] .veui-button:not(:disabled).focus-ring {
  color: #3389e3;
}
.veui-dropdown[ui~="link"] .veui-button:not(:disabled):active {
  color: #2e7aca;
}
.veui-dropdown[ui~="link"] .veui-button > .veui-icon {
  float: none;
  margin-top: 0;
  vertical-align: middle;
}



