/* Metrics */
.veui-alert-box .veui-dialog-content {
  width: 350px;
}
.veui-alert-box .veui-dialog-content-head {
  display: none;
}
.veui-alert-box .veui-dialog-content-body {
  text-align: center;
  margin-top: 30px;
}
.veui-alert-box .veui-dialog-content-foot {
  padding-top: 30px;
  padding-bottom: 20px;
}
.veui-alert-box-icon {
  width: 30px;
  height: 30px;
}
.veui-alert-box-title {
  color: #333;
  font-weight: 400;
  margin-top: 20px;
  margin-bottom: 14px;
  line-height: 1;
  font-size: 16px;
}
.veui-alert-box-content {
  color: #666;
  font-size: 14px;
}
.veui-alert-box[ui~="info"] .veui-alert-box-icon {
  color: #3998fc;
}
.veui-alert-box[ui~="error"] .veui-alert-box-icon {
  color: #ff5b5b;
}
.veui-alert-box[ui~="success"] .veui-alert-box-icon {
  color: #5bc49f;
}



