/* Metrics */
.veui-radio {
  display: inline-block;
  color: #666;
  font-size: 14px;
  cursor: pointer;
}
.veui-radio input,
.veui-radio .veui-icon {
  display: none;
}
.veui-radio-label {
  vertical-align: middle;
}
.veui-radio:hover {
  color: #333;
}
.veui-radio-box {
  display: inline-block;
  position: relative;
  height: 16px;
  width: 16px;
  border: 1px solid #666;
  background-color: #fff;
  margin-right: 8px;
  border-radius: 50%;
  vertical-align: middle;
  font-size: 14px;
}
.veui-radio:hover .veui-radio-box {
  border-color: #333;
}
.veui-radio :checked + .veui-radio-box {
  border-color: #333;
}
.veui-radio :checked + .veui-radio-box::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  background-color: #333;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  right: 50%;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.5);
}
.veui-radio.veui-disabled {
  cursor: not-allowed;
}
.veui-radio.veui-disabled .veui-radio-label {
  color: #999;
}
.veui-radio :disabled + .veui-radio-box {
  border-color: #dbdbdb;
  background-color: #eee;
}
.veui-radio :disabled + .veui-radio-box::before {
  background-color: #dbdbdb;
}
.veui-radio[ui~="small"] {
  font-size: 12px;
}
.veui-radio[ui~="small"] .veui-radio-box {
  margin-right: 6px;
  font-size: 12px;
  height: 12px;
  width: 12px;
}
.veui-radio[ui~="small"] .veui-radio-box::before {
  width: 12px;
  height: 12px;
}



