/* Metrics */
.veui-button-group {
  display: inline-block;
}
.veui-button-group .veui-button {
  position: relative;
  z-index: 0;
  margin-left: -1px;
}
.veui-button-group .veui-button:first-child {
  margin-left: 0;
}
.veui-button-group .veui-button:hover:not(.veui-disabled),
.veui-button-group .veui-button:focus:not(.veui-disabled) {
  z-index: 2;
}
.veui-button-group .veui-button[ui~="primary"],
.veui-button-group .veui-button[ui~="alt"] {
  margin: 0 0 0 1px;
  border-right-width: 0;
  border-left-width: 0;
}
.veui-button-group .veui-button[ui~="primary"]:first-child,
.veui-button-group .veui-button[ui~="alt"]:first-child {
  margin-left: 0;
}
.veui-button-group-disabled .veui-button,
.veui-button-group-disabled .veui-button[ui] {
  border-right-color: #fff;
  border-left-color: #fff;
}
.veui-button-group-disabled .veui-button:first-child,
.veui-button-group-disabled .veui-button[ui]:first-child {
  border-left-color: transparent;
}
.veui-button-group-disabled .veui-button:last-child,
.veui-button-group-disabled .veui-button[ui]:last-child {
  border-right-color: transparent;
}
.veui-button-group-disabled .veui-button[ui~="primary"],
.veui-button-group-disabled .veui-button[ui][ui~="primary"],
.veui-button-group-disabled .veui-button[ui~="alt"],
.veui-button-group-disabled .veui-button[ui][ui~="alt"] {
  border-right-width: 0;
  border-left-width: 0;
}



